import React, { Component } from 'react';
import './App.css';
import Paint from './Paint';
import AppAppBar from './modules/views/AppAppBar';
import AppFooter from './modules/views/AppFooter';
import LinearProgress from '@material-ui/core/LinearProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';

function getImage(canvas) {
    return canvas.toDataURL();
}

class App extends Component {
    state = {
        data: null,
        refresh: false,
        answerReady: false
    };

    componentDidMount() {
        // Call our fetch function below once the component mounts
        const script = document.createElement("script");

        // script.src = "https://algorithmia.com/v1/clients/js/algorithmia-0.2.0.js";
        // script.type = "text/javascript";
        // script.async = true;
        //
        // document.body.appendChild(script);
        //
        // this.callBackendAPI()
        //     .then(res => this.setState({ data: res.express }))
        //     .catch(err => console.log(err));
    }
    // Fetches our GET route from the Express server. (Note the route we are fetching matches the GET route from server.js
    callBackendAPI = async () => {
        let canvas= document.getElementById("draw");
        let img = await getImage(canvas);
        console.log(img);
    };

    clear() {
        let canvas= document.getElementById("draw");
        let ctx = canvas.getContext("2d");
        ctx.clearRect(0, 0, canvas.width, canvas.height);
    }
    handleClose = () => {
        this.setState({ answerReady: false });
    };

    predict() {
        this.setState({loading: true});
        this.callBackendAPI()
            .then(res => {
                this.setState({ data: res, answerReady: true, loading: false })
            })
            .catch(err => console.log(err));
    }

    render() {
        return (
            <div className="App">
                <Dialog
                    open={this.state.answerReady}
                    onClose={this.handleClose}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">{"Prediction"}</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                            {this.state.data}
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} color="primary" autoFocus>
                            Close
                        </Button>
                    </DialogActions>
                </Dialog>
                <AppAppBar clear={this.clear.bind(this)}/>
                <Paint canvas={this.state.canvas} ctx={this.state.ctx}/>
                {this.state.loading? <LinearProgress/>: ''}
                <AppFooter predict={this.predict.bind(this)}/>
            </div>
        );
    }
}

export default App;
